body {
    margin: 0;
    padding: 0;
    font-family: "Neucha", cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* my color palette variables */

:root {
    --text-color: #222f3e;
    --section-color: #54a0ff;
    --border-color: #c8d6e5;
    --custom-red: #ff6b6b;
}

/* basic reset */

* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
}

html {
    font-size: 14px;
}

body {
    font-weight: normal;
    font-style: normal;
    color: #222f3e;
    color: var(--text-color);
}

ul {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
    margin: 0;
}

section {
    font-size: 1rem;
}

a {
    color: #222f3e;
    color: var(--text-color);
    text-decoration: none;
}

a:active {
    text-decoration: none;
}

a:active,
a:focus,
a:visited {
    color: #222f3e;
    color: var(--text-color);
}

img {
    max-width: 100%;
    height: auto;
}

p {
    line-height: 1.5;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

.row {
    display: flex;
}

.filter-panel {
    max-width: 25%;
    flex-grow: 1;
}

.characters-panel {
    max-width: 75%;
    flex-grow: 3;
}

.loader-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background-color: #fff;
}

.loader {
    background-image: url("/images/loader.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
    width: 50px;
    height: 50px;
}

